import React from "react";
import "./FullPage.css";
import { useSpring, animated } from "react-spring";
export default function FullPage(props) {
  const colors = {
    0: "#8c3e53",
    1: "#FE5F55",
    2: "#28666E",
  };

  const styles = useSpring({
    config: { duration: 600 },
    background: colors[props.current],
  });

  return (
    <animated.div className="script-box" style={styles}>
      {props.children}
    </animated.div>
  );
}
